.swiper-container {
  background-color: #06d2cc;
  padding-inline: 80px;
  border: solid 1px black;
}
@media (max-width: 900px) {
  .swiper-container {
    padding-inline: 25px;
  }
}

.swiper_body-title {
  padding-block-start: 80px;
  padding-block-end: 40px;
  text-align: center;
}
@media (max-width: 900px) {
  .swiper_body-title {
    padding-block-start: 60px;
    padding-block-end: 30px;
  }
}

.swiper-title {
  font-size: 55px;
  text-align: center;
}
@media (max-width: 900px) {
  .swiper-title {
    font-size: 28px;
  }
}

.swiper {
  padding: 80px 0;
  position: relative;
}

.swiper-wrapper {
  position: relative;
  display: flex;
  gap: 30px;
  padding-block-end: 251px;
}

.slide-link {
  border: solid 1px black;
  box-shadow: 5px 3px 0 0px rgb(0, 0, 0);
  padding-block: 10px;
  padding-inline: 18px;
  border-radius: 100px;
}
.slide-link:hover {
  cursor: pointer;
  transform: scale(1.05);
}

.swiper-slide {
  width: 406px !important;
  background-color: white;
  border-radius: 10px;
  border: 1px solid black;
  box-shadow: 5px 5px 0 2px rgb(0, 0, 0);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.swiper-slide:hover {
  transform: scale(1.1);
}

.slide_body-img {
  width: 100%;
}

.slide-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slide-body {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
  padding-block: 40px;
}
@media (max-width: 900px) {
  .slide-body {
    padding-block: 30px;
  }
}

.swiper-button-prev::after,
.swiper-button-next::after {
  content: none !important;
}

.swiper-slide.swiper-slide-active {
  transform: translateY(151px) !important;
  z-index: 10;
}
@media (max-width: 1400px) {
  .swiper-slide.swiper-slide-active {
    transform: none !important;
  }
}

.swiper-button-prev,
.swiper-button-next {
  position: absolute !important;
  top: 469px !important;
}
.swiper-button-prev:hover,
.swiper-button-next:hover {
  transform: scale(1.1);
}
.swiper-button-prev:hover:active,
.swiper-button-next:hover:active {
  transform: translateY(10px);
}
@media (max-width: 900px) {
  .swiper-button-prev,
  .swiper-button-next {
    top: 500px !important;
  }
}

.swiper-button-prev {
  left: 239px !important;
}
@media (max-width: 900px) {
  .swiper-button-prev {
    left: 75px !important;
  }
}

.swiper-button-next {
  right: 239px !important;
}
@media (max-width: 900px) {
  .swiper-button-next {
    right: 75px !important;
  }
}

.swiper-button-prev img.img-prev,
.swiper-button-next img.img-next {
  width: 100px !important;
  height: auto !important;
  max-width: none !important;
  max-height: none !important;
}